.app__newsletter{
	padding: 2rem 4rem;
	z-index: 1;
	border: 1px solid rgba(220, 202, 135, 0.2);
	background: var(--color-black);
}

.app__newsletter-heading{
	text-align: center; 
}

.app__newsletter-input{
	flex-direction: row;
	margin-top: 3rem;
}

.app__newsletter-input input{
	width: 620px;
	border: 1px solid var(--color-golden);
	border-radius: 5px;
	font-size: 1rem;
	font-family: var(--font-base);
	color: var(--color-white);

	margin-right: 2rem;
	padding: 1rem 1.2rem;
	background: var(--color-black);
}

.app__newsletter-input button{
	width: max-content;
}

/* !media */
@media screen and (min-width: 2000px) {
	.app__newsletter-input input{
		font-size: 2rem;
	}
}

@media screen and (max-width: 990px) {
	.app__newsletter-input{
		flex-direction: column;
		width: 100%;
	}

	.app__newsletter-input input{
		margin: 0 0 2rem 0;
		width: 100%;
	}
}

@media screen and (max-width: 650px) {
	.app__newsletter{
		padding: 2rem 0;
		border: none;
	}
}

@media screen and (max-width: 300px) {
	.app__newsletter-heading h3{
		font-size: 32px;
		line-height: 50px;
	}
}