.app__footer{
	width: 100%;
	position: relative;
	z-index: 1;

	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;

	background: var(--color-black);
	padding-top: 0;
}

.app__footer-links,
.footer__copyright{
	z-index: 1;
}

.app__footer-links{
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;

	margin-top: 5rem;
	padding: 0 2rem;
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work{
	flex: 1;
	text-align: center;
	margin: 1rem;
}

.app__footer-links_logo img:first-child{
	width: 210px;
	margin-bottom: .75rem;
}

.app__footer-links_work p:nth-child(2n + 1){
	margin-bottom: 1rem;
}

.app__footer-links_icons{
	margin: .5rem 0;
}

.app__footer-links_icons svg{
	color: var(--color-white);
	margin: 0.5rem;
	font-size: 24px;
	cursor: pointer;
	transition: .5s ease;
}

.app__footer-links_icons svg:hover{
	color: var(--color-golden);
}

.app__footer-headtext{
	font-weight: 400;
	font-size: 32px;
	line-height: 41px;
	letter-spacing: 0.04em;
	text-transform: capitalize;
	color: var(--color-crimson);
	font-family: var(--font-base);

	margin-bottom: 1rem;
}

.footer__copyright{
	margin-top: 3rem;
}

@media screen and (min-width: 2000px) {
	.app__footer-headtext{
		font-size: 51px;
		line-height: 61px;
	}
}

@media screen and (max-width: 1150px) {
	.app__footer-links{
		align-items: center;
		flex-direction: column;
		padding: 0;
	}

	.app__footer-links_contact,
	.app__footer-links_logo,
	.app__footer-links_work{
		margin: 2rem 0;
		width: 100%;
	}
}
@media screen and (max-width: 650px) {
	.app__footer{
		padding: 0 0 2rem 0;
	}
}

@media screen and (max-width: 450px) {
	.footer__copyright{
		text-align: center;
	}
}

@media screen and (max-width: 350px) {
	.app__footer-links_logo img:first-child{
		width: 80%;
	}
}

